// API configuration for different environments
const API_CONFIG = {
  // Base URL - empty string for relative paths in production (Vercel) and Vercel dev
  // For regular local development, we'd use localhost:5000, but since we're using Vercel dev, we use relative paths
  BASE_URL: '',
};

// Helper function to build API URLs
export const apiUrl = (endpoint) => `${API_CONFIG.BASE_URL}${endpoint}`;

export default API_CONFIG;